/**
 * @fileoverview This file defines the Schedule page for the employee dashboard.
 * It displays the employee's weekly work schedule in a clear, tabular format.
 */
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
  CardDescription,
  CardFooter,
} from "@/components/ui/card";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Calendar, Clock } from "lucide-react";

/**
 * Renders the employee's weekly schedule page.
 * It displays the schedule in a table and highlights the current day.
 * @returns {JSX.Element} The schedule page component.
 */
export default function SchedulePage() {
  // --- DATABASE INTEGRATION POINT ---
  // In a real application, this would be fetched from a database for the current user and week.
  const schedule = [
    { day: 'Monday', date: 'Aug 19, 2024', timeIn: '09:00 AM', timeOut: '06:00 PM', break: '12:00 PM - 01:00 PM', hours: 8 },
    { day: 'Tuesday', date: 'Aug 20, 2024', timeIn: '09:00 AM', timeOut: '06:00 PM', break: '12:00 PM - 01:00 PM', hours: 8 },
    { day: 'Wednesday', date: 'Aug 21, 2024', timeIn: '09:00 AM', timeOut: '06:00 PM', break: '12:00 PM - 01:00 PM', hours: 8 },
    { day: 'Thursday', date: 'Aug 22, 2024', timeIn: '09:00 AM', timeOut: '06:00 PM', break: '12:00 PM - 01:00 PM', hours: 8 },
    { day: 'Friday', date: 'Aug 23, 2024', timeIn: '09:00 AM', timeOut: '06:00 PM', break: '12:00 PM - 01:00 PM', hours: 8 },
    { day: 'Saturday', date: 'Aug 24, 2024', timeIn: 'OFF', timeOut: 'OFF', break: '-', hours: 0 },
    { day: 'Sunday', date: 'Aug 25, 2024', timeIn: 'OFF', timeOut: 'OFF', break: '-', hours: 0 },
  ];

  // Get today's day name to highlight in the schedule.
  const today = new Date().toLocaleDateString("en-US", { weekday: "long" });
  const todaysSchedule = schedule.find(s => s.day === today);
  const totalWeeklyHours = schedule.reduce((acc, s) => acc + s.hours, 0);

  return (
    <div className="space-y-6">
      <h1 className="text-3xl font-bold font-headline">My Schedule</h1>
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Calendar />
            This Week's Schedule
          </CardTitle>
          <CardDescription>
            Your work schedule for the current week.
          </CardDescription>
        </CardHeader>
        <CardContent>
          {schedule.length > 0 ? (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Day</TableHead>
                  <TableHead>Date</TableHead>
                  <TableHead>Time In</TableHead>
                  <TableHead>Time Out</TableHead>
                  <TableHead>Break</TableHead>
                  <TableHead className="text-right">Hours</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {schedule.map((item) => (
                  <TableRow key={item.day} className={item.day === today ? 'bg-accent' : ''}>
                    <TableCell className="font-medium">
                      {item.day}
                      {/* Highlight the current day with a "Today" badge */}
                      {item.day === today && <Badge variant="default" className="ml-2">Today</Badge>}
                    </TableCell>
                    <TableCell>{item.date}</TableCell>
                    <TableCell>{item.timeIn}</TableCell>
                    <TableCell>{item.timeOut}</TableCell>
                    <TableCell>{item.break}</TableCell>
                    <TableCell className="text-right">{item.hours > 0 ? `${item.hours}` : "-"}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          ) : (
            <p className="text-muted-foreground text-center">No schedule has been published for this week.</p>
          )}
        </CardContent>
        <CardFooter className="flex justify-between items-center">
            {/* Display total hours for the current day */}
            <div className="text-sm text-muted-foreground flex items-center gap-2">
                <Clock />
                <span>Today's Total Hours: <span className="font-bold text-foreground">{todaysSchedule?.hours ?? 0}</span></span>
            </div>
            {/* Display total hours for the entire week */}
            <div className="font-semibold">
                Total Weekly Hours: {totalWeeklyHours}
            </div>
        </CardFooter>
      </Card>
    </div>
  );
}
